@import "../../styles/main";

.how_works {
  padding: 80px 20px 60px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;

  h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
  }

  &_tile {
    
  }

  img {
    width: 60%;
  }

  @include media-query-md {
    img {
      width: 60%;
    }
    p {
      font-size: 1.25rem;
      max-width: 80%;
    }
  }

  @include media-query-lg {
    grid-template-columns: 1fr 1fr 1fr;

    &_tile:nth-of-type(1) {
      grid-row-start: 1;
      grid-row-end: 3;
    }
    img {
      width: 100%;
    }
  }
}

.icon_wrapper {
  height: 60px;
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000;
  border-radius: 8px;
  background-color: #00b0ff;
  font-size: 2rem;
  margin-bottom: 10px;
}
